.slider-container {
    width: 100%;
    margin: 20px 0;
    background-color: white;
    // height: 60vh;
    // background-color: coral;
    overflow: hidden;
    display: flex;
    position: relative;

    .arrow {
        z-index: 2;
        width: 50px;
        height: 50px;
        background-color: #fff7f7;
        color: black;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        cursor: pointer;
        opacity: 0.7;
    }

    .arrow.left {
        left: 10px;
    }

    .arrow.right {
        right: 10px;
    }

    .wrapper {
        height: 100%;
        display: flex;
        transform: translateX(0vw);
        transition: all 1.5s ease;
        gap: 20px;
        margin: 0 10px;

        .slide {
            display: flex;
            // align-items: center;
            // width: 100vw;

            .image-container {
                width: 50vw;
                height: 400px;


                // flex: 1;

